<template>
  <div class="pagination">
    <button @click="btnChange">首页</button>
    <button @click="btnChange">上一页</button>
    <button v-if="jduge">.....</button>
    <button
      v-for="(btn, index) in pagebtns"
      :key="index"
      :class="[{currentPage: btn == currentPage}, 'pagebtn']"
      @click="btnChange(btn)"
    >{{btn}}</button>
    <button @click="btnChange">下一页</button>
  </div>              
</template>


<script>                
import $ from 'jquery'
export default {
  name: 'Pagination',
  data() {
    return {
      pagebtns: [1, 2, 3, 4, 5, '.....'],
      currentPage: 1,
      jduge: false                
    }
  },
  methods: {
    btnChange(page) {
      if ((typeof page) != 'number') {
        console.log(page)
        switch (page.target.innerText) {
          case '上一页':
            let ss = $('button.currentPage').prev().click()
            break
          case '下一页':
            $('button.currentPage').next().click()
            break
          case '首页':
            this.pagebtns = [1, 2, 3, 4, 5, '.....']
            this.btnChange(1)
            break
          default:
            break
        }
        return
      }
      this.currentPage = page
      page > 4 ? this.jduge = true : this.jduge = false
      if (page == this.pagebtns[4]) {
        this.pagebtns.shift()//移除第一个
        this.pagebtns.splice(4, 0, this.pagebtns[3] + 1)//添加最后一个
      } else if (page === this.pagebtns[0] && page !== 1) {
        //反向再操作一波
        this.pagebtns.unshift(this.pagebtns[0] - 1)
        this.pagebtns.splice(5, 1)
      }
      this.$emit('handle-list',this.currentPage)
    }
  }
}
</script>


<style scoped>
.pagination {
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: white;
  padding: 6px 20px;
  border-radius: 5px;
  /*box-shadow: 0px 2px 9px #888888;*/
  border: 1px solid #888888;
}

button {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #778087;
  border-radius: 3px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 0 2px;
  width: 55px;
  height: 29px;
}

.pagebtn {
  position: relative;
  bottom: 1px;
  width: 40px;
  margin: 0 4px;
}

.currentPage {
  color: white;
  background-color: #1f1b1b;
}
</style>



